.sequence-card{
    border-radius: 4px;
background: var(--text-icon-text-icon-5, #FFF);
border: 1px solid var(--text-text-icon-03, #DCDEE1);
box-shadow: 0px 6px 16px 0px rgba(219, 219, 219, 0.25);
}
.sequence-title{
    display: inline-flex;
    padding: 2px 8px;
    flex-direction: column;
    align-items: flex-start;
    gap: 10px;
    color: var(--ux-primary, #4680FF);
    text-align: center;
    border-radius: 4px;
    background: rgba(33, 73, 255, 0.08);
    font-family: Alibaba PuHuiTi;
    font-size: 12px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;

}
.sequence-title-notedit{
    display: inline-flex;
padding: 2px 8px;
flex-direction: column;
align-items: flex-start;
gap: 10px;
border-radius: 4px;
background: var(--UX-bg_module, #F8F9FA);
}
.sequence-title-preview{
    width:100%;
    height: 40px;
    display: inline-flex;
    padding: 10px 10px;
    flex-direction: column;
    color:white;
    text-align: center;

    width: 100%;
    height: fit-content;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    overflow: hidden;
    line-height: 18px;
    font-size: 14px;
}

.sequence-caption{
    width: 100%;
    height: 52px;
    max-height: 76px;
    flex-shrink: 0;
    overflow: hidden;
    color: #7E7E7E;
    text-align: center;
    text-overflow: ellipsis;
    /* white-space: nowrap; */
    font-family: Alibaba PuHuiTi;
    font-size: 12px;
    font-style: normal;
    font-weight: 300;
    line-height: normal;
}
.sequence-caption-preview{
    font-size: 1rem;
    white-space: normal;
    color: #000;
    width: 100%;
    height: 52px;
    max-height: 72px;
    overflow: hidden;
}
.sequence-calliope{
    padding-top: 10px;
    justify-content: space-between;
    box-shadow: 2px 2px rgba(0,0,0,.2);
}

.sequence-chart{
    display: flex;
    justify-content: center;
}

.editing-outlining{
    border-left: 2px solid var(--text-text-icon-0, #252931);
    border-bottom: 2px solid var(--ux-primary, #4680FF);
    border-top: 2px solid var(--ux-primary, #4680FF);
    border-right: 2px solid var(--ux-primary, #4680FF);
}
.editing-notoutline{
    border: 2px solid var(--ux-primary, #4680FF);

}
.notedit-outlining{
    border-left: 2px solid var(--text-text-icon-0, #252931);
}
.notedit-notoutline{
    border: 1px solid var(--ux-secondary-01, #C6DDFF);
}


.trend:before {
    background-image: linear-gradient(135deg, #5f9c92,#b4f0e6);
    clip-path: polygon(100% 0, 0 0, 100% 100%);
}

.distribution:before {
background-image: linear-gradient(135deg, #8db14f,#d2fa8d);
clip-path: polygon(100% 0, 0 0, 100% 100%);
